import React, { Component } from "react";
import axios from "axios";
import { withRouter } from 'react-router-dom';

class Users extends Component {
  state = {
    users: [],
    inputValue: ''
  };

  componentDidMount() {
    axios
      .get(process.env.REACT_APP_API_URL + '/user')
      .then(res => {
        const users = res.data;
        let existingUsers = [];
        console.log(users);
        users.map(user => existingUsers.push({ name: user.name, totalScore: this.CalculateTotalScore(user) }));
        this.setState({ users: existingUsers }, () => {
          console.log("state: ", this.state);
        });
      })
      .catch(err => {
        console.log("error while fetching users. ", err);
      });
  }

  CalculateTotalScore(user) {
    if (user == null || user.days == null) {
      return 0;
    }
    const days = [...user.days];
    const reducer = (present, next) => present + next.dailyScore;
    const totalScore = days.reduce(reducer, 0);
    // // console.log("TotalScore: ", totalScore);
    return totalScore;
  }

  handleChange = event => {
    this.setState({ inputValue: event.target.value });
  }

  handleSubmit = event => {
    console.log("new user: ", this.state.inputValue);
    event.preventDefault();
    this.props.history.push('/' + this.state.inputValue);
  }

  handleUserClicked = (userName, event) => {
    event.preventDefault();
    console.log("handleUserClicked: ", userName);
    this.props.history.push('/' + userName);
  }

  render() {
    return (
      <div>
        {this.state.users.map(user => (
          <div className="row mt-2" key={user.name}>
            <div className="col" >
              <button type="button" onClick={(e) => this.handleUserClicked(user.name, e)} className="btn btn-primary" style={{ width: '75%' }}>
                <span className="display-4">
                  {user.name} <span className="badge badge-light">{user.totalScore}</span>
                </span>
              </button>
            </div>
          </div>
        ))}
        <div className="row mt-2">
          <div className="col">
            <form onSubmit={this.handleSubmit}>
              <input type="text" value={this.state.value} onChange={this.handleChange} />
              <input type="submit" className="btn btn-primary ml-2" value="add new user"></input>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(Users);
